import { defineComponent, ref, onBeforeMount, defineAsyncComponent } from 'vue'
import ServiceEngagement from '@/service/ServiceEngagement/ServiceEngagement'
const DrawerTable = defineAsyncComponent(() => import('../DrawerTable/DrawerTable.vue'))

export default defineComponent({
  name: 'CARightMenu',
  data () {
    return {
      show: false,
      istabledrawer: false
    }
  },
  components: {
    'drawer-table': DrawerTable
  },
  props: {
    selectedChartData: {
      default: ''
    },
    seetingArray: {
      default: ''
    },
    dateObj: {
      default: {}
    }
  },
  setup (props) {
    const loader = ref(false)
    const engType = ref([])
    const totalCount = ref('')
    const tableData = ref({})
    const isShowTabel = ref(false)
    const engFilter = ref([]) as any
    const selectedDateObj = ref(props.dateObj) as any;
    onBeforeMount(async () => {
      loader.value = true
      await dashboardCountList()
      loader.value = false
      initFilter()
    })
    // CountList
    const dashboardCountList = async () => {
      const url = '/charts/engagement/type/count'
      const postData = {
        fromtime: '2021-01-01T00:00:00.000Z',
        totime: '2021-12-31T23:59:59.000Z'
      }
      const res = await ServiceEngagement.commonEndpoint('post', url, postData)
      if (res.apiStatus) {
        engType.value = res && Array.isArray(res.result) ? res.result : []
        totalCount.value = res && res.total ? res.total : 0
      }
      console.log('-------- Engagement Type Count List ---------', res)
    }
    const gotoDetails = async (data:any,seeting:any) => {
      console.log('gotoDetails', data)
      const filterObj = seeting.find((a:any) => a.key === 'eng-by-status')
      const param = {
        fromtime: selectedDateObj.value.starttime,
        totime: selectedDateObj.value.endtime
      }
      tableData.value = {
        selectedType: data.engagementTypeId,
        chartTittle: data.engagementType + 'Engagement Details',
        postData: param,
        chartSetting: filterObj,
        tabelEndpoint: '/charts/engagement/details/type',
        tableFilterData: engFilter.value
      }
      isShowTabel.value = !isShowTabel.value
    }
    const initFilter = async () => {
      engFilter.value = [
        {
          id: '1',
          'filter-name': 'engagement-type-list',
          title: 'Engagement Type ',
          filterLabel: [],
          chooseNames: [],
          chooseId: [],
          filterKey: 'type'
        },
        {
          id: '2',
          'filter-name': 'product-list',
          title: 'Product',
          filterLabel: [],
          chooseNames: [],
          chooseId: [],
          filterKey: 'product'
        }
      ]
    }
    return {
      loader,
      dashboardCountList,
      engType,
      totalCount,
      gotoDetails,
      tableData,
      isShowTabel,
      initFilter,
      selectedDateObj
    }
  }

})
